import React, { useState } from "react";
import { useNavigate } from 'react-router-dom';
import { jwtDecode } from 'jwt-decode'; // Importacin corregida
import './Login.css';

function Login() {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault(); 
  
    try {
      const response = await fetch('https://api.bhsmx.com/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, password }),
      });
  
      const data = await response.json();
  
      if (response.ok) {
        // Guardar el token en localStorage
        localStorage.setItem('token', data.token);
  
        // Decodificar el token para obtener el tipo de usuario
        const decodedToken = jwtDecode(data.token);
        const userType = decodedToken.type; // Obtener el tipo de usuario del token
  
        // Redireccin basada en el tipo de usuario
        if (userType === 'admin') {
          navigate('/dashboard'); // Redirige a la pgina de admin si es un administrador
        } else if (userType === 'user') {
          navigate('/certificados'); // Redirige a la pgina de certificados si es un usuario
        } else if (userType === 'client') {
          navigate('/client'); // Redirige a la pgina de cliente si es un cliente
        } else {
          throw new Error('Tipo de usuario desconocido'); // Mensaje de error genrico
        }
      } else {
        throw new Error(data.message || 'Error en la autenticacin'); // Lanza un error si la respuesta no es OK
      }
    } catch (error) {
      setErrorMessage(error.message || 'Ocurri un error inesperado'); // Manejo del mensaje de error
    }
  };
  
  return (
    <main>
      <div className="login-Container">
        <div className="Main-Login-Container">
          <h2>Iniciar Sesin</h2>
          <form onSubmit={handleSubmit}>
            <div className="username">
              <input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
              />
              <label>Nombre de Usuario</label>
            </div>
            <div className="password">
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
              <label>Contrasea</label>
            </div>
            {errorMessage && <div className="error">{errorMessage}</div>}
            <div className="forgot">Has olvidado tu contrasea?</div>
            <button className="submit-login" type="submit">
              Enviar
            </button>
          </form> 
        </div>
      </div>
    </main>
  );
}

export default Login;
