const items=[
    { title: 'Unidad de potencia', img: [img1], text: '<p>El mantenimiento de una Unidad de Potencia es crucial para asegurar su correcto funcionamiento y evitar fallas en los sistemas que alimenta. A continuacin, se describe el proceso de mantenimiento tpico:</p><ul>Inspeccin visual general: Se revisa todo el sistema en busca de posibles fugas, corrosin, acumulacin de suciedad o partculas contaminantes, y se asegura que todas las conexiones estn firmes y sin daos.</ul><ul>Revisin del nivel y estado del fluido hidrulico: El fluido es el alma del sistema hidrulico. Se revisa el nivel del aceite y su calidad, verificando si ha perdido sus propiedades (viscosidad, color o presencia de partculas). En caso de contaminacin o deterioro, se procede al reemplazo.</ul><ul>Cambio de filtros: Los filtros hidrulicos se encargan de mantener el sistema libre de contaminantes. Se revisan y, si es necesario, se sustituyen los filtros de aceite, de aire y de retorno para asegurar un flujo limpio.</ul><ul>Inspeccin y prueba de la bomba hidrulica: Se comprueba el estado de la bomba para asegurarse de que genera la presin adecuada y no tiene fugas. Si se detectan ruidos inusuales o un rendimiento deficiente, puede ser necesario su mantenimiento o reemplazo.</ul><ul>Revisin de las vlvulas: Se inspeccionan las vlvulas de control, presin y direccin para garantizar que estn funcionando correctamente. Esto incluye la limpieza o el reemplazo si muestran signos de desgaste o mal funcionamiento.</ul><ul>Verificacin del motor elctrico o disel: Si la HPU est accionada por un motor, se realiza una revisin completa de este componente, verificando los niveles de aceite, el estado de los filtros de combustible y aire, y los componentes elctricos para garantizar su correcto arranque y operacin.</ul><ul>Inspeccin de los acumuladores: Si la unidad tiene acumuladores hidrulicos, se verifica la presin interna y se revisa su estado estructural para asegurar que puedan almacenar y liberar energa sin problemas.</ul><ul>Revisin de los manmetros y sensores: Se comprueba que los instrumentos de medicin, como manmetros y sensores de temperatura y presin, estn funcionando correctamente para monitorear el rendimiento de la unidad de potencia.</ul><ul>Pruebas de presin y flujo: Despus del mantenimiento, se realizan pruebas de rendimiento para asegurar que la unidad est generando la presin y el flujo requeridos segn las especificaciones operativas.</ul><p>Este proceso de mantenimiento preventivo asegura que la Unidad de Potencia Hidrulica funcione eficientemente, reduciendo el riesgo de fallas y prolongando la vida til de los equipos conectados.</p>'},
    { title: 'Cilindro Hidrulico', img: [img1], text: '<p>El mantenimiento de un cilindro hidrulico es fundamental para garantizar su ptimo rendimiento y evitar fallas en los sistemas donde se utiliza. A continuacin, se describen los pasos tpicos en el proceso de mantenimiento:</p><ul>Inspeccin visual: Se realiza una revisin exhaustiva del cilindro hidrulico para detectar posibles fugas de aceite, daos en la superficie del vstago o seales de desgaste en las conexiones y juntas.</ul><ul>Desmontaje del cilindro: Se procede a desmontar el cilindro hidrulico para evaluar el estado de los componentes internos. Esto incluye la extraccin del vstago, pistn, sellos y otras partes mviles.</ul><ul>Limpieza interna y externa: Todas las piezas desmontadas se limpian a fondo para eliminar residuos de aceite, polvo, partculas metlicas o cualquier tipo de contaminacin que pueda interferir con el funcionamiento.</ul><ul>Inspeccin del vstago y pistn: Se examina el vstago en busca de rayones, abolladuras o corrosin que puedan comprometer el sellado. Tambin se verifica el estado del pistn para asegurarse de que est en condiciones ptimas.</ul><ul>Reemplazo de sellos y juntas: Los sellos y juntas son componentes crticos para mantener la estanqueidad del sistema hidrulico. Se reemplazan si muestran signos de desgaste, endurecimiento o deformacin.</ul><ul>Revisin del alojamiento: Se verifica que el alojamiento del cilindro no presente deformaciones o desgaste excesivo, ya que esto podra causar fugas o fallos en el funcionamiento.</ul><ul>Montaje del cilindro: Una vez inspeccionados y reemplazados los componentes daados, se vuelve a montar el cilindro, asegurndose de que todas las piezas estn correctamente alineadas y ajustadas.</ul><ul>Pruebas de presin y funcionamiento: Finalmente, se somete el cilindro hidrulico a pruebas de presin para verificar que no haya fugas y que funcione adecuadamente bajo las condiciones de trabajo.</ul><p>Este proceso de mantenimiento preventivo asegura que el cilindro hidrulico opere de manera eficiente, prolongando su vida til y evitando problemas en el sistema hidrulico en el que est instalado.</p>'},
    { title: 'Circuitos Hidrulicos', img: [img1], text: '<p>El mantenimiento de los circuitos hidrulicos es esencial para garantizar su funcionamiento eficiente y prolongar la vida til del sistema. Un mantenimiento adecuado previene fallas, reduce tiempos de inactividad y optimiza la operacin de los equipos. A continuacin, se detallan los pasos generales del mantenimiento a circuitos hidrulicos:</p><ul>Inspeccin visual general del sistema: Se revisan todas las lneas, conexiones y componentes del circuito en busca de fugas de aceite, daos en las tuberas, corrosin o conexiones sueltas. Las fugas en los sistemas hidrulicos pueden ser indicativas de problemas graves en el futuro si no se corrigen a tiempo.</ul><ul>Revisin de los filtros: Se inspeccionan y, si es necesario, se sustituyen los filtros del sistema hidrulico. Los filtros juegan un papel crucial en la eliminacin de contaminantes del fluido hidrulico que podran daar otros componentes del circuito.</ul><ul>Verificacin del fluido hidrulico: Se revisa el nivel y la calidad del aceite hidrulico. Es fundamental que el fluido est limpio y con las propiedades correctas para un funcionamiento ptimo. Si el fluido ha perdido sus caractersticas de viscosidad o est contaminado, es necesario su reemplazo.</ul><ul>Revisin de las mangueras y tuberas: Las mangueras y tuberas del circuito hidrulico se revisan en busca de signos de desgaste, hinchazn o grietas. Si alguna de las mangueras o tuberas muestra seales de dao, debe ser reemplazada para evitar fallos y fugas.</ul><ul>Inspeccin de bombas y motores: Las bombas y motores son componentes clave en un circuito hidrulico. Se verifica que no haya ruidos inusuales, prdidas de presin o flujo insuficiente. El desgaste de estos componentes puede provocar una reduccin en la eficiencia del sistema y aumentar los costos operativos.</ul><ul>Verificacin de vlvulas: Se revisan las vlvulas de control de presin, direccin y flujo. Estas deben operar de forma suave y sin bloqueos. Cualquier mal funcionamiento en las vlvulas puede afectar el rendimiento del circuito.</ul><ul>Revisin de los cilindros hidrulicos: Los cilindros se inspeccionan en busca de fugas, golpes o deformaciones. El sellado de los cilindros debe estar en buen estado para evitar fugas internas o externas que puedan comprometer la fuerza del sistema.</ul><ul>Pruebas de presin y flujo: Se realizan pruebas para comprobar que la presin y el flujo en el circuito hidrulico se encuentran dentro de los parmetros establecidos. Esto permite identificar cualquier prdida de rendimiento o anomala que podra necesitar ajustes o reparaciones.</ul><ul>Lubricacin de componentes mviles: Los componentes mviles, como juntas o cilindros, deben ser lubricados segn las especificaciones del fabricante para evitar el desgaste prematuro.</ul><ul>Comprobacin de sensores y manmetros: Se revisa el correcto funcionamiento de los sensores de presin y los manmetros, asegurndose de que proporcionan lecturas precisas para el control adecuado del circuito.</ul><ul>Pruebas de funcionamiento: Una vez finalizado el mantenimiento, se realizan pruebas de funcionamiento para verificar que todo el circuito est operando de manera eficiente y sin fallas.</ul><p>Este proceso de mantenimiento garantiza la eficiencia y confiabilidad del circuito hidrulico, minimizando el riesgo de fallas y mejorando el rendimiento general del sistema.</p>'},
    { title: 'Spoolers', img: [img2, img3], text: '<p>El mantenimiento de un spooler, as como de sus componentes principales como el motor hidrulico, la bomba hidrulica y las vlvulas, es esencial para garantizar su ptimo funcionamiento y prolongar su vida til. Este proceso incluye las siguientes acciones:</p><ul>Inspeccin general del spooler: Se revisa la estructura para detectar signos de desgaste, corrosin o daos fsicos que puedan comprometer su operacin.</ul><ul>Mantenimiento del motor hidrulico: Se inspeccionan las conexiones, se limpian los componentes, y se verifica el estado del aceite hidrulico para asegurar una correcta lubricacin. Adems, se chequean sellos y juntas para prevenir fugas.</ul><ul>Revisin de la bomba hidrulica: Se inspecciona la bomba hidrulica para garantizar que no haya fallas en el flujo del fluido. Esto incluye verificar la presin de salida, revisar el nivel del aceite hidrulico y asegurarse de que no haya contaminacin o partculas en el fluido.</ul><ul>Inspeccin y ajuste de vlvulas: Las vlvulas son clave para controlar el flujo y la presin en el sistema hidrulico del spooler. Se realiza una prueba de presin para asegurarse de que las vlvulas abran y cierren correctamente, y se ajustan si es necesario.</ul><ul>Pruebas de funcionamiento: Tras las inspecciones y ajustes, se realizan pruebas operativas del spooler bajo condiciones controladas para asegurarse de que todas las piezas trabajen en conjunto de manera eficiente y sin problemas.</ul><p>Este mantenimiento preventivo permite asegurar que el spooler mantenga un rendimiento confiable, reduciendo tiempos de inactividad no planificados y mejorando la eficiencia del equipo en operaciones crticas.</p>'}
]

function SistemaPotencia() {
  return (
   <div className='Container-SistemaPotencia'>
    <h2>Mantenimiento a Sistemas Hidraulicos de Potencia</h2>
    <SelectContentBox  
        items={items}
        />
   </div>
  );
}

export default SistemaPotencia;
